import {DataSopNode} from '@polygonjs/polygonjs/src/engine/nodes/sop/Data';
import {DeleteSopNode} from '@polygonjs/polygonjs/src/engine/nodes/sop/Delete';
import {EventsSopNode} from '@polygonjs/polygonjs/src/engine/nodes/sop/Events';
import {MergeSopNode} from '@polygonjs/polygonjs/src/engine/nodes/sop/Merge';
import {NormalsSopNode} from '@polygonjs/polygonjs/src/engine/nodes/sop/Normals';
import {NullSopNode} from '@polygonjs/polygonjs/src/engine/nodes/sop/Null';
import {ObjectMergeSopNode} from '@polygonjs/polygonjs/src/engine/nodes/sop/ObjectMerge';
