// sop
import {CSS2DObjectSopOperation} from '@polygonjs/polygonjs/src/engine/operations/sop/CSS2DObject'
import {AddSopOperation} from '@polygonjs/polygonjs/src/engine/operations/sop/Add'
import {AttribAddMultSopOperation} from '@polygonjs/polygonjs/src/engine/operations/sop/AttribAddMult'
import {AttribCopySopOperation} from '@polygonjs/polygonjs/src/engine/operations/sop/AttribCopy'
import {AttribCreateSopOperation} from '@polygonjs/polygonjs/src/engine/operations/sop/AttribCreate'
import {AttribFromTextureSopOperation} from '@polygonjs/polygonjs/src/engine/operations/sop/AttribFromTexture'
import {AttribNormalizeSopOperation} from '@polygonjs/polygonjs/src/engine/operations/sop/AttribNormalize'
import {AttribPromoteSopOperation} from '@polygonjs/polygonjs/src/engine/operations/sop/AttribPromote'
import {CenterSopOperation} from '@polygonjs/polygonjs/src/engine/operations/sop/Center'
import {CircleSopOperation} from '@polygonjs/polygonjs/src/engine/operations/sop/Circle'
import {IcosahedronSopOperation} from '@polygonjs/polygonjs/src/engine/operations/sop/Icosahedron'
import {InstanceSopOperation} from '@polygonjs/polygonjs/src/engine/operations/sop/Instance'
import {MaterialSopOperation} from '@polygonjs/polygonjs/src/engine/operations/sop/Material'
import {MergeSopOperation} from '@polygonjs/polygonjs/src/engine/operations/sop/Merge'
import {NullSopOperation} from '@polygonjs/polygonjs/src/engine/operations/sop/Null'
import {ObjectPropertiesSopOperation} from '@polygonjs/polygonjs/src/engine/operations/sop/ObjectProperties'
import {PlaneSopOperation} from '@polygonjs/polygonjs/src/engine/operations/sop/Plane'
import {PolarTransformSopOperation} from '@polygonjs/polygonjs/src/engine/operations/sop/PolarTransform'
import {SphereSopOperation} from '@polygonjs/polygonjs/src/engine/operations/sop/Sphere'
import {TransformSopOperation} from '@polygonjs/polygonjs/src/engine/operations/sop/Transform'
export class PolyRegister {
	static run() {
		AllExpressionsRegister.run(Poly);
		// cop
		Poly.registerNode(ImageCopNode);
		// event
		Poly.registerNode(CameraOrbitControlsEventNode);
		// mat
		Poly.registerNode(MeshBasicMatNode);
		Poly.registerNode(MeshBasicBuilderMatNode);
		Poly.registerNode(MeshLambertMatNode);
		// obj
		Poly.registerNode(CopObjNode);
		Poly.registerNode(GeoObjNode);
		Poly.registerNode(HemisphereLightObjNode);
		Poly.registerNode(MaterialsObjNode);
		Poly.registerNode(NullObjNode);
		Poly.registerNode(PerspectiveCameraObjNode);
		Poly.registerNode(SpotLightObjNode);
		// rop
		Poly.registerNode(CSS2DRendererRopNode);
		Poly.registerNode(WebGLRendererRopNode);
		// sop
		Poly.registerNode(AttribCreateSopNode);
		Poly.registerNode(Circle3PointsSopNode);
		Poly.registerNode(CopySopNode);
		Poly.registerNode(DataSopNode);
		Poly.registerNode(DeleteSopNode);
		Poly.registerNode(EventsSopNode);
		Poly.registerNode(MergeSopNode);
		Poly.registerNode(NormalsSopNode);
		Poly.registerNode(NullSopNode);
		Poly.registerNode(ObjectMergeSopNode);
		Poly.registerNode(OperationsComposerSopNode);
		Poly.registerNode(PointSopNode);
		Poly.registerNode(RenderersSopNode);
		// sop
		Poly.registerOperation(CSS2DObjectSopOperation);
		Poly.registerOperation(AddSopOperation);
		Poly.registerOperation(AttribAddMultSopOperation);
		Poly.registerOperation(AttribCopySopOperation);
		Poly.registerOperation(AttribCreateSopOperation);
		Poly.registerOperation(AttribFromTextureSopOperation);
		Poly.registerOperation(AttribNormalizeSopOperation);
		Poly.registerOperation(AttribPromoteSopOperation);
		Poly.registerOperation(CenterSopOperation);
		Poly.registerOperation(CircleSopOperation);
		Poly.registerOperation(IcosahedronSopOperation);
		Poly.registerOperation(InstanceSopOperation);
		Poly.registerOperation(MaterialSopOperation);
		Poly.registerOperation(MergeSopOperation);
		Poly.registerOperation(NullSopOperation);
		Poly.registerOperation(ObjectPropertiesSopOperation);
		Poly.registerOperation(PlaneSopOperation);
		Poly.registerOperation(PolarTransformSopOperation);
		Poly.registerOperation(SphereSopOperation);
		Poly.registerOperation(TransformSopOperation);
		// custom configuration
		configurePolygonjs(Poly);
	}
}
